import React, {useState, useEffect,  Suspense } from 'react';
import * as serviceWorker from './serviceWorker';
import { createBrowserHistory } from "history";
import {
  BrowserRouter as Router,
  Switch,
  Route,
} from "react-router-dom";

import "assets/scss/material-kit-react.scss?v=1.9.0";

// pages for this product
import { makeStyles } from '@material-ui/core/styles';

import CircularProgress from '@material-ui/core/CircularProgress';

import Header from "components/Header/Header.js";
import HeaderLinks from "components/Header/HeaderLinks.js";

import axiosInstance from './axios.js';

const About = React.lazy(() => import("views/about/About.jsx"));
const Compte = React.lazy(() => import("views/compte/Compte.jsx"));
const LoginPage = React.lazy(() => import("views/compte/Login.js"));
const RegisterPage = React.lazy(() => import("views/compte/register"));
const Commande = React.lazy(() => import("views/commande/Commande.jsx"));
const Home = React.lazy(() => import('views/Home/Home.js'));
const Catelogue = React.lazy(() => import('views/Catalogue/Catelogue.js'));

var hist = createBrowserHistory();
/* 
const baseURL = 'http://192.168.1.100:8000/';*/
let baseURL ;
if (window.location.hostname === 'localhost' || window.location.hostname === '127.0.0.1'){
  baseURL = `http://${window.location.hostname}:8000/`;
} else{
  baseURL = 'https://auroracomputing.herokuapp.com/';
}

/* const page =  [
  { title: 'Acceuil', url: '/liste-action' },
  { title: 'Catalogue', url: '#' },
  { title: 'A ppropos', url: '#' },
  { title: 'Connexion', url: '#' },
];
 */
const App = () => {
  const [user, setUser] = useState();

  useEffect(() => {
    handleUserAuth();
	}, []);

  const handleUserAuth = () => { 
    console.log('je suis lalaalalal app')
    if(localStorage.getItem('refresh_token')){
      axiosInstance.get(`${baseURL}client-api/identity/`).then((res) => {
        console.log('cat 2')
        console.log(res.data)
        if(res.data !== 'None'){
          console.log(res.data)
          setUser(res.data)
        }
      });
    }
  }

  return (
    <Router history = { hist } >
      <Header
          brand="Brobro lacation"
          user={user} 
          rightLinks={<HeaderLinks  user={user} />}
          fixed
          color='transparent'
          changeColorOnScroll={{
            color: "white",
            height: 300
          }}
        />
        <Suspense fallback={<LinearBuffer/>}>
          <Switch>
            <Route exact path="/about">
              <About baseURL={baseURL}/>
            </Route>
            <Route exact path="/catalogue">
              <Catelogue baseURL={baseURL}/>
            </Route>
            <Route path="/commande/:carId">
              <Commande  baseURL={baseURL} user={user} handleUserAuth={handleUserAuth}/>
            </Route>
            <Route path="/compte">
              <Compte baseURL={baseURL}   user={user} />
            </Route>
            <Route path="/login">
              <LoginPage baseURL={baseURL} handleUserAuth={handleUserAuth}/>
            </Route>
            <Route path="/client-register-page">
              <RegisterPage baseURL={baseURL}/>
            </Route> 
            <Route path="/">
              <Home baseURL={baseURL}/>
            </Route>
        </Switch> 
      </Suspense>
    </Router>
  );
}

export default App;


function LinearBuffer() {
  const useStyles = makeStyles({
  root: {
    position: 'fixed',
    height: '100vh',
    width: '100vw',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    zIndex: 99
  },
});
  const classes = useStyles();
  return (
    <div className={classes.root}>
      <CircularProgress />
    </div>
  );
}


serviceWorker.unregister();