import React, {useEffect} from "react";
// react components for routing our app without refresh
import { Link } from "react-router-dom";

// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";

import { useHistory } from 'react-router-dom';

// @material-ui/icons
import ExitToAppRoundedIcon from '@material-ui/icons/ExitToAppRounded';
import PermIdentityIcon from '@material-ui/icons/PermIdentity';

// core components
import CustomDropdown from "components/CustomDropdown/CustomDropdown.js";
import styles from "assets/jss/material-kit-react/components/headerLinksStyle.js";

import axiosInstance from 'axios';
import { useLocation } from 'react-router-dom';

import {grey} from '@material-ui/core/colors';

const useStyles = makeStyles(styles);

export default function HeaderLinks(props) {
  const classes = useStyles();
  const location = useLocation();

  return (
    <List className={classes.list}>
      <ListItem className={classes.listItem}>
          <Link to={"/"}
          color="transparent"
          className={classes.navLink}
          onClick={props.toggleMenu}
          style={
            location.pathname === '/' || location.pathname === '/brobro'  || location.pathname === '/brobro/'
              ? props.colorChange ?{backgroundColor: grey[100]} : {backgroundColor: 'rgba(100, 100, 100, 0.45)'}
              : {}
          }>
            {
              location.pathname === '/'  || location.pathname === '/brobro'  || location.pathname === '/brobro/'
                ? props.setNavColor('transparent')
                : props.setNavColor('white')
            }
            Acceuil
          </Link>
      </ListItem>
      <ListItem className={classes.listItem}>
            <Link to="/catalogue"
              color="transparent"
              className={classes.navLink}
              onClick={props.toggleMenu}
              style={
                location.pathname === '/catalogue' ? {backgroundColor: grey[100]} : {}
              }>
              Catalogue
            </Link>
      </ListItem>
      <ListItem className={classes.listItem}>
          <Link to={"/about"}
          color="transparent"
          className={classes.navLink}
          onClick={props.toggleMenu}
          style={
            location.pathname === '/about' ? {backgroundColor: grey[100]} : {}
          }>
            A ppropos
          </Link>
      </ListItem>
      <ListItem className={classes.listItem}  style={{
        border: '2px solid #27A', 
        borderRadius: 8,
        }}
        style={{
          border: '2px solid #27A' ,
          borderRadius: 8,
          backgroundColor: location.pathname === '/compte' ? grey[100] : null
        }}>
        <Compte classes={classes}  user={props.user} toggleMenu={props.toggleMenu}/>
      </ListItem>
    </List>
  );
}


const Compte = ({classes, user, ...props}) => {
  
	const history = useHistory();

  const handleLogout = (e) => {
    e.preventDefault();
		const response = axiosInstance.post('http://localhost:8000/api/user/logout/blacklist/', {
			refresh_token: localStorage.getItem('refresh_token'),
		});
		localStorage.removeItem('access_token');
		localStorage.removeItem('refresh_token');
		axiosInstance.defaults.headers['Authorization'] = null;

    window.location.reload();
    history.push('/');
	}
  
  console.log('headerLink '+ user)
  return (
    <>
      {user
          ? <CustomDropdown 
          noLiPadding
          buttonProps={{
            className: classes.navLink,
            color: "transparent"
          }}
          dropdownList={[
            <Link to="/compte" className={classes.dropdownLink}
            onClick={props.toggleMenu}>
              Mon compte
            </Link>,
            <a
              className={classes.dropdownLink}
              onClick={handleLogout}
            >
              Deconnexion
            </a>
          ]}
        > 
          <PermIdentityIcon/> {user ? user.first_name.slice(0, 1) + '. ' + user.user_name : 'Inconnu' }
        </CustomDropdown>
          : <CustomDropdown
          noLiPadding
          buttonText="Login"
          buttonProps={{
            className: classes.navLink,
            color: "transparent"
          }}
          buttonIcon={ExitToAppRoundedIcon}
          dropdownList={[
            <Link to="/login" className={classes.dropdownLink}
            onClick={props.toggleMenu}>
              Se connecter
            </Link>,
            <Link to="/client-register-page" className={classes.dropdownLink}
            onClick={props.toggleMenu}>
              S'inscrire
            </Link>
          ]}
        />}
    </>
  );
}
